<template>
  <div style="padding: 20px 20px 20px 20px;width: 100%">
    <div>
      任务编号：{{data.taskId}}
    </div>
    <div>
      任务名称：{{data.taskName}}
    </div>
    <div>
      开始时间：{{data.timeBegin}}
    </div>
    <div>
      结束时间：{{data.timeEnd}}
    </div>
    <div>
      执行人员：{{data.userName}}
    </div>
    <div>
      反馈内容：{{data.data}}
    </div>
    <el-table
        :data="data.device"
        style="width: 100%">
      <el-table-column
          prop="equipmentName"
          label="设备名称"
          width="180">
      </el-table-column>
      <el-table-column
          prop="childEquipment"
          label="部件名称"
          width="180">
      </el-table-column>
      <el-table-column
          prop="parameter"
          label="参数名称"
          width="180">
      </el-table-column>
      <el-table-column
          label="点检结果"
          width="180">

        <template slot-scope="scope">
          <span :style="{ color: scope.row.result === '正常' ? 'green' : 'red' }">
            {{ scope.row.type === '判断' ? scope.row.realData2 : scope.row.realData1 }}
          </span>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>
<script>
export default {
  name: "TaskCheckDevice",
  data() {
    return {
      data:{},
    };
  },
  created() {
    this.load(this.$route.query.taskId)
  },
  methods: {
    async load(taskId){
      try {
        const recordTaskResponse = await this.$request.get('/recordTask/get', {
          params: { taskId: taskId }
        });
        const numericValueResponse = await this.$request.get('/realData/get', {
          params: { taskNum: taskId }
        });

        this.data = recordTaskResponse.data;
        this.data.device = numericValueResponse.data;
        console.log(this.data);
      } catch (error) {
        console.error(error);
      }
    }
  }
}
</script>
